.sidebar{
  width: 230px;
  height: 100vh;
  left: 0px;
  /* background: #1b1b1b; */
  transition: left 0.4s ease;
  margin-right: 30px;
}
.sidebar.show{
  left: 0px;
}
.sidebar .text{
  color: black;
  font-size: 25px;
  font-weight: 600;
  line-height: 65px;
  text-align: left;
  /* background: #1e1e1e; */
  letter-spacing: 1px;
}
nav ul{
  /* background: #1b1b1b; */
  height: 100%;
  width: 100%;
  list-style: none;
}
nav ul li{
  line-height: 60px;
  border-top: 1px solid rgba(255,255,255,0.1);
}
nav ul li:last-child{
  border-bottom: 1px solid rgba(255,255,255,0.05);
}
nav ul li a{
  position: relative;
  color: black;
  text-decoration: none;
  font-size: 18px;
  /* padding-left: 40px; */
  font-weight: 500;
  display: block;
  width: 100%;
  font-family: Arial, Helvetica, sans-serif;
  /* border-left: 3px solid transparent; */
}
nav ul li.active a{
  /* color: cyan; */
  /* background: #1e1e1e; */
  /* border-left-color: cyan; */
}
nav ul li a:hover{
  /* background: #1e1e1e; */
}
nav ul ul{
  position: static;
  display: none;
}
nav ul .feat-show.show{
  display: block;
}
nav ul .serv-show.show1{
  display: block;
}
nav ul ul li{
  line-height: 42px;
  border-top: none;
}
nav ul ul li a{
  font-size: 14px;
  font-family: Arial, Helvetica, sans-serif;
  color: #000;
  padding-left: 20px;
}
nav ul li.active ul li a{
  color: #e6e6e6;
  background: #1b1b1b;
  border-left-color: transparent;
}
nav ul ul li a:hover{
  /* color: cyan!important; */
  /* background: #1e1e1e!important; */
}
nav ul li a span{
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
  font-size: 22px;
  transition: transform 0.4s;
}
nav ul li a span.rotate{
  transform: translateY(-50%) rotate(-180deg);
}
.content{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  color: #202020;
  z-index: -1;
  text-align: center;
}
.content .header{
  font-size: 45px;
  font-weight: 600;
}
.content p{
  font-size: 30px;
  font-weight: 500;
}
